import React, { Component } from 'react';
import PropTypes from 'prop-types'; // 

import '../style/layout/sidebar.css';

import { Link } from 'react-router-dom';
class SideBar extends Component {

    logout = () => {
        //   jwt_token 
        localStorage.removeItem('jwt_token');
        localStorage.removeItem('name');

        //   
        window.location.href = '/'; //     
    };

    render() {
        const { setting } = this.props; // 

        return (
            <nav id="side-bar">
                <div className="welcome">
                    {setting.logindUserName}, !
                </div>

                <div className="nav-bar">
                    {setting.allMenus.map((menu, index) => (
                        <div className="select-item" key={index}>
                            <div className="category-name">{menu.categoryName}</div>
                            <div className="items">
                                {menu.subMenus.map((subMenu, subIndex) => (
                                    <Link key={subIndex} to={`${subMenu.link}`} className={`menu ${subMenu.isSelected ? 'active' : ''}`}>{subMenu.subMenuName}</Link>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>

                <div className="bottom-nav"><a onClick={this.logout}> </a></div>
            </nav>
        );
    }
}

SideBar.propTypes = {
    setting: PropTypes.object.isRequired,
};

export default SideBar;
