import React, { useState } from 'react';
import Header from '../../component/Header.js';
import SideBar from '../../component/SideBar';
import '../../style/customer/AsView.css';


import axios from 'axios';

const AsView = () => {
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [errors, setErrors] = useState({
        title: '',
        content: '',
    });
    const validation = () => {
        if (title.length < 5) {
            setErrors({ ...errors, title: "5  ." });
            return false;
        } else if (title.length > 50) {
            setErrors({ ...errors, title: "50  ." });
            return false;
        } else if (title.replaceAll(" ", "").length === 0) {
            setErrors({ ...errors, title: "   ." });
            return false;
        }

        if (content.length < 5) {
            setErrors({ ...errors, content: "5  ." });
            return false;
        } else if (content.length > 100) {
            setErrors({ ...errors, content: "100  ." });
            return false;
        } else if (content.replaceAll(" ", "").length === 0) {
            setErrors({ ...errors, content: "   ." });
            return false;
        }

        return true;
    }

    const registration = async () => {
        try {
            
            if(!validation()) return;
    
    
            const res =  await axios.post('/api/repair/registrationrepair', {
                problemTitle: title,
                problemComment: content
            },
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );

            if(res.data.results === " .") {
                alert(" .")
                setContent('');
                setTitle('');
                setErrors({
                    title: '',
                    content: '',
                });
            }
        } catch (e) {
            console.log(e);
            
        }
    };

    return (
        <>
            <Header content=""></Header>
            <SideBar setting={


                {
                    "logindUserName":  window.localStorage.getItem("name"),
                    "allMenus": [
                        {
                            "categoryName": "",
                            "subMenus": [
                                {
                                    "subMenuName": "",
                                    "link": "/customer/board",
                                    "isSelected": false
                                },
                                {
                                    "subMenuName": "A/S",
                                    "link": "/customer/as",
                                    "isSelected": true
                                }
                            ]
                        },
                        {
                            "categoryName": "",
                            "subMenus": [
                                {
                                    "subMenuName": " ",
                                    "link": "/customer/myinfo",
                                    "isSelected": false
                                },
                                {
                                    "subMenuName": "  ",
                                    "link": "/customer/faq",
                                    "isSelected": false
                                }
                            ]
                        }

                    ]


                }
            }
            />  
            <section id="main">
                <div className="page-title">A/S </div>
                <div id="as-form">
                    <h2>A/S </h2>
                    <label htmlFor="customerName">:</label>
                    <input type="text" id="customerName" name="customerName" value={title} onChange={(e) => setTitle(e.target.value)} autoFocus />
                    <span className='invaild-feedback show' style={{"fontSize": "13px"}}>{errors.title}</span>
                    <label htmlFor="asDescription"> :</label>
                    <textarea id="asDescription" name="asDescription" value={content} onChange={(e) => setContent(e.target.value)}></textarea>
                    <span className='invaild-feedback show' style={{"fontSize": "13px"}}>{errors.content}</span>

                    <button type="submit" onClick={registration}>A/S </button>
                </div>
            </section>

        </>

    );
};

export default AsView;
