import React, { useState, useEffect } from 'react';
import Header from '../../component/Header.js';
import SideBar from '../../component/SideBar.js';
import '../../style/admin/repaire.css';
import 'bootstrap/dist/css/bootstrap.min.css'; // Bootstrap  import

import axios from 'axios';


const RepairReception = () => {

    const [repairs, setRepairs] = useState([]);
    const [admins, setAdmins] = useState([]);




    const [error, setError] = useState({});
    const [selectedAdmin, setSelectedAdmin] = useState('');
    const [meetingTime, setMeetingTime] = useState('');
    const [selectedMode, setSelectedMode] = useState('allData');


    const getToday = () => {
        var options = { timeZone: 'Asia/Seoul' };
        var koreaTime = new Date().toLocaleString('en-US', options);

        var formattedKoreaTime = new Date(koreaTime).toISOString().slice(0, 19);

        return formattedKoreaTime;
    }

    useEffect(() => {
        setMeetingTime(getToday())
        getAllAdmin();
        getAllRepairStatus();
        //getsearchRepair();
    }, []);


    const validation = (idx) => {


        if (selectedAdmin === "") {
            setError({ ...error, [idx]: { "admin": " ." } });
            return false;
        } else if (selectedAdmin === "") {
            setError({ ...error, [idx]: { "admin": " ." } });
            return false;
        }

        return true;
    }

    //  A/S  
    const getAllRepairStatus = async () => {

        try {
            const response = await axios.get('/api/repair/getRepairStatus',
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );
    
            setRepairs(response.data);

        } catch(e) {
            console.log(e)
        }


    };

    //     
    const getAllAdmin = async () => {
        try {
            const response = await axios.get('/api/user/getAllAdmins',
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );

            setAdmins(response.data);
        } catch (e) {
            console.log(e)
        }
    };

    // 
    const registerRepair = async (reId) => {
        try {
            await axios.post('/api/repair/processrepair',
                {
                    repairIdx: reId,
                    adminId: selectedAdmin,
                    visitDate: meetingTime
                },
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );
            getAllRepairStatus();
        } catch (e) {
            console.log(e)
        }

    };

    //  
    const complete = async (reId) => {
        try {
            await axios.post('/api/repair/completeRepair',
                reId,
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );

            getAllRepairStatus();
        } catch (e) {
            console.log(e)
        }
    };


    //    
    const adminChange = (event) => {
        setError({})
        setSelectedAdmin(event.target.value);
    };

    //    
    const timeChange = (event) => {
        setMeetingTime(event.target.value);
    };

    //     ,   
    const selectItem = (item) => {
        if (item.adminId === null && item.finished === 0) {
            return;
        }
        setSelectedAdmin(item.adminId);
        setMeetingTime(item.visitDate);
    };

    //  ,   
    const edit = async (reId, idx) => {
        console.log(error)
        if(!validation(idx)) return;

        try {
            const res = await axios.put('/api/repair/editAdminIdVisitDate',
                {
                    idx: reId,
                    adminId: selectedAdmin,
                    visitDate: meetingTime
                },
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );

            if (res.data.result === "FAILED") {
                alert("     .")
            } else {
                alert("     .")
                getAllRepairStatus();
            }
        } catch (e) {
            console.log(e)
        }

    };
    


    const getFilteringData = async (type) => {
        console.log(type)   
        setSelectedMode(type);
        try{
            const response = await axios.get("/api/repair/getRepairFiltering?type="+type,
            {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                },
            }
        );
            console.log(response.data)
            setRepairs(response.data)

        }catch(e){
            console.log(e)
        }
        
    
    };

    const getsearchRepair = async (getValue) => {
        console.log(getValue);
        try{
            const response = await axios.get(`/api/repair/searchRepair?userId=${getValue}&type=${selectedMode}`,
            {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                }
            }
            );
            console.log(response.data)
            
            setRepairs(response.data)

        }catch(e){
            console.log(e)
        }

        

    };

    
    

    return (
        <>
            <Header content="Management" />
            <SideBar setting={
                {
                    "logindUserName": window.localStorage.getItem("name"),
                    "allMenus": [
                        {
                            "categoryName": " ",
                            "subMenus": [
                                {
                                    "subMenuName": "",
                                    "link": "/manager/members",
                                    "isSelected": false
                                },
                                {
                                    "subMenuName": "",
                                    "link": "/manager/managers",
                                    "isSelected": false
                                }
                            ]
                        },
                        {
                            "categoryName": "",
                            "subMenus": [
                                {
                                    "subMenuName": "A/S",
                                    "link": "/manager/repaireprocess",
                                    "isSelected": true
                                },
                                {
                                    "subMenuName": "",
                                    "link": "/manager/board",
                                    "isSelected": false
                                },
                                {
                                    "subMenuName": "  ",
                                    "link": "/customer/board",
                                    "isSelected": false
                                }
                            ]
                        },
                        {
                            "categoryName": " ",
                            "subMenus": [
                                {
                                    "subMenuName": "  ",
                                    "link": "/manager/asklist",
                                    "isSelected": false
                                }
                            ]
                        }

                    ]


                }}
            />  
            <section id="main">
                <div className="page-title">A/S   </div>
                <div className="container-md">


                    <div className="search-container" style={{"width":"100%"}}>
                        <div style={{"width":"100%", "display" : 'flex'}}>
                            
                            <input style={{"flex" : 9}} type='text' placeholder=' ' onChange={(e) => getsearchRepair(e.target.value)} />

                            <button onClick={null} className="btn btn-dark"></button>
                        </div>
                        

                        <div className="mode-btn-wrap">
                            <button onClick={() => getFilteringData("allData")} className='mode-btn'></button>
                            <button onClick={() => getFilteringData("waiting")} className='mode-btn'> </button>
                            <button onClick={() => getFilteringData("willVisit")} className='mode-btn'> </button>
                            <button onClick={() => getFilteringData("finished")} className='mode-btn'> </button>
                        </div>

                    </div>
                    
                   


                   

                    
                    {repairs.map((item, index) => (
                        <div className="collapse-box" key={index}>
                            <div className="information-btn-container">
                                <a onClick={() => selectItem(item)} data-bs-toggle="collapse" href={`#collapseContainer${index + 1}`} role="button" aria-expanded="false" aria-controls="collapseContainer">
                                    <div className="as-information">
                                        <div className="as-title">
                                            {item.adminId === null && item.finished === 0 &&
                                                <span style={{ color: 'red' }}>[ ]</span>
                                            }

                                            {item.adminId != null && item.finished === 0 &&
                                                <span style={{ color: 'orange' }}>[ ]</span>
                                            }

                                            {item.adminId != null && item.finished === 1 &&
                                                <span style={{ color: 'green' }}>[ ]</span>
                                            }

                                            {item.problemTitle}
                                        </div>
                                        <div className="as-profile">
                                            <div> : {item.name}</div>
                                            <div> : {item.phone}</div>
                                        </div>
                                    </div>
                                </a>
                            </div>
                            <div className="collapse" id={`collapseContainer${index + 1}`}>
                                <div className="card card-body">
                                    <div className="as-sub-title">[    ]</div>
                                    <table className="member_table">
                                        <thead>
                                            <tr>
                                                <td></td>
                                                <td></td>
                                                <td></td>
                                                <td></td>
                                                <td></td>
                                                <td></td>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <th>{item.customerUserId}</th>
                                                <th>{item.name}</th>
                                                <th>{item.address}</th>
                                                <th>{item.phone}</th>
                                                <th>{item.email}</th>
                                                <th>{item.gender}</th>
                                            </tr>
                                        </tbody>
                                    </table>

                                    <div className="as-sub-title">[   ]</div>
                                    <div className="as-information">
                                        <div className="desc"><span style={{ color: 'red' }}>-</span> {item.problemComment}</div>
                                    </div>

                                    <div className="as-process">
                                        <select className="form-select form-select-lg" aria-label="Large select example" defaultValue={selectedAdmin} onChange={adminChange}>
                                            <option value="" selected>  </option>
                                            {admins.map((admin, idx) => (
                                                <><option value={`${admin.userId}`}>{admin.userName}</option></>
                                            ))}
                                        </select>

                                        {error[item.idx] !== undefined && 
                                            <div className="invalid-feedback show">{error[item.idx].admin}</div>
                                        }

                                        <input
                                            type="datetime-local"
                                            id="meeting-time"
                                            className="meeting-time"
                                            name="meeting-time"
                                            value={meetingTime}
                                            min="2023-12-22T16:04"
                                            onChange={timeChange}
                                        />

                                        {/* <div className="invalid-feedback show">{error[item.idx].meetingTime}</div> */}


                                        {item.adminId === null && item.finished === 0 &&
                                            <button className="as-p-btn" onClick={() => registerRepair(item.idx)}></button>
                                        }

                                        {item.adminId != null && item.finished === 0 &&
                                            <div className='flex-btn-wrap'>
                                                <button className='flex-btn orange' onClick={() => edit(item.ridx, item.idx)}> </button>
                                                <button className='flex-btn green' onClick={() => complete(item.idx)}> </button>
                                            </div>
                                        }

                                        {item.adminId != null && item.finished === 1 &&
                                            <button className="as-p-btn" onClick={() => registerRepair(item.idx)} disabled></button>
                                        }

                                        {/* {item.adminId == null ? <button className="as-p-btn" onClick={() => registerRepair(item.idx)}></button> : <button className="as-p-btn" onClick={() => registerRepair(item.idx)} disabled></button>} */}
                                    </div>
                                </div>
                            </div>
                            <div className="line"></div>
                        </div>
                    ))}


                </div>
            </section>
        </>
    );
};

export default RepairReception;