import React, { useState, useEffect } from 'react';
import Header from '../../component/Header';
import SideBar from '../../component/SideBar';

import '../../style/customer/MyInfoView.css';
import { useParams } from 'react-router-dom';


import axios from 'axios';

const MyInfoView = () => {
    const [userInformation, setUserInformation] = useState({});
    const [boards, setBoards] = useState([]);
    const [repairs, setRepairs] = useState([]);

    const { paramName } = useParams();


    const getUserInformation = async () => {
        try {
            const response = await axios.get('/api/user/getUserByUserId?userId=' + paramName,
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );
            setUserInformation(response.data.result);


        } catch (e) {
            console.log(e)
        }




    };

    const getBoard = async () => {
        try {
            
            const response = await axios.get('/api/board/getBoardByUserIdx?userId=' + paramName,
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );
    
            setBoards(response.data);
            console.log(response.data);
        } catch (e) {
            console.log(e);
        }
    };

    const getRepairs = async () => {
        try {
            const response = await axios.get('/api/repair/getRepairStatusByUserId?userId=' + paramName,
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );
    
            setRepairs(response.data);
            console.log(response.data);
            
        } catch (e) {
            console.log(e);
        }
    };


    const deleteBoard = async (boardIdx) => {
        try {
            console.log(boardIdx)
            var res = await axios.delete('/api/board/deleteBoard?boardIdx=' + boardIdx,
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );
            console.log(res.data)
            getBoard();
            
        } catch (e) {
            console.log(e);
        }
    };

    const addAdmin = async () => {
        try {
            var res = await axios.post('/api/user/addRole',
                {
                    "userId": paramName,
                    "role": "Admin"
                },
                {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + window.localStorage.getItem("jwt_token"),
                    }
                }
            );

            if (res.status === 200) {
                alert("  .")

            }
        } catch (e) {
            alert("  .")
        }
    };

    useEffect(() => {
        getUserInformation();
        getBoard();
        getRepairs();
    }, []);


    return (
        <>
            <Header content="Management"></Header>
            <SideBar setting={
                {
                    "logindUserName": window.localStorage.getItem("name"),
                    "allMenus": [
                        {
                            "categoryName": " ",
                            "subMenus": [
                                {
                                    "subMenuName": "",
                                    "link": "/manager/members",
                                    "isSelected": true
                                },
                                {
                                    "subMenuName": "",
                                    "link": "/manager/managers",
                                    "isSelected": false
                                }
                            ]
                        },
                        {
                            "categoryName": "",
                            "subMenus": [
                                {
                                    "subMenuName": "A/S",
                                    "link": "/manager/repaireprocess",
                                    "isSelected": false
                                },
                                {
                                    "subMenuName": "",
                                    "link": "/manager/board",
                                    "isSelected": false
                                },
                                {
                                    "subMenuName": "  ",
                                    "link": "/customer/board",
                                    "isSelected": false
                                }
                            ]
                        },
                        {
                            "categoryName": " ",
                            "subMenus": [
                                {
                                    "subMenuName": "  ",
                                    "link": "/manager/asklist",
                                    "isSelected": false
                                }
                            ]
                        }

                    ]


                }}
            />            <section id="main">
                <div className="page-title"> </div>


                <div className="info_section">
                    <h2> </h2>
                    <dl>
                        <dt>ID:</dt>
                        <dd>{userInformation.userId}</dd>
                    </dl>
                    <dl>
                        <dt>:</dt>
                        <dd>{userInformation.userName}</dd>
                    </dl>
                    <dl>
                        <dt>:</dt>
                        <dd>{userInformation.userAddress}</dd>
                    </dl>
                    <dl>
                        <dt>:</dt>
                        <dd>{userInformation.userPhone}</dd>
                    </dl>
                    <dl>
                        <dt>:</dt>
                        <dd>{userInformation.userEmail}</dd>
                    </dl>
                    <dl>
                        <dt>:</dt>
                        <dd>{userInformation.userGender}</dd>
                    </dl>

                    <dl>
                        <button className='add-r' onClick={addAdmin}> </button>
                    </dl>
                </div>


                <div className="section">
                    <h2>A/S  </h2>

                    {repairs.map((item, index) => (
                        <div className="box">
                            <h3>A/S  #{index + 1}</h3>
                            <p>: {item.createAt.split("T")[0]}</p>
                            <p>
                                 : {
                                    item.adminId == null ?
                                        <span className="status-ing">[ ]</span>
                                        :
                                        <><span className="status-ing" style={{ "color": "green" }}>[ ]</span><span>     : {item.visitDate.replaceAll("T", " ")}</span></>
                                }
                            </p>

                            <p>: {item.problemComment}</p>
                        </div>
                    ))}

                </div>


                <div className="section">
                    <h2>  </h2>
                    <div className="my_inquiry_list">
                        {boards.map((item, index) => (
                            <div className="box">
                                <h3> : {item.boardTitle} ( : {item.createAt})</h3>
                                <div className="btn-group">
                                    <button className="btn-delete" onClick={() => deleteBoard(item.boardIdx)}></button>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

        </>

    );
};

export default MyInfoView;
